* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  border-style: none;
}

.mobile-hidden.watch-item {
  display: none;
}

.catalog-container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 32px;
}

.catalog-header {
  width: 100%;
  max-width: 450px;
  letter-spacing: -.02em;
  color: #1e2827;
  font-size: 40px;
  font-weight: 600;
  line-height: 1.1;
}

.watch-img {
  width: 276px;
  height: 274px;
}

.watch-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  margin-bottom: 80px;
  display: flex;
}

.watch-item {
  flex-basis: calc(25% - 69px);
  justify-content: center;
  margin-bottom: 64px;
  display: inline-block;
}

.watch-description {
  max-width: 276px;
  margin-top: 14px;
}

.watch-name {
  color: var(--black, #141414);
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: -.28px;
  margin: 4px;
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
}

.watch-price {
  color: rgba(20, 20, 20, .5);
  letter-spacing: -.24px;
  font-family: Epilogue, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.btn-container {
  justify-content: flex-end;
  display: flex;
}

.catalog-btn {
  width: 122px;
  height: 50px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background: #1e2827;
  border: none;
  border-radius: 8px;
  font-family: Epilogue, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.catalog-btn:hover, .catalog-btn:focus {
  background: #1e2827;
  border: 1px solid #fff;
  border-radius: 8px;
}

.card-container {
  width: 276px;
  height: 325px;
  perspective: 1000px;
  background-color: rgba(0, 0, 0, 0);
}

.card {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.card:hover, .card:focus {
  transform: rotateY(180deg);
}

.front, .back {
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  position: absolute;
}

.front {
  justify-content: center;
  align-items: center;
  display: flex;
}

.back {
  color: #fff;
  background: #1e2827;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  padding: 30px 26px;
  display: flex;
  transform: rotateY(180deg);
}

.back-card, .back-card-item {
  width: auto;
}

.back-card-description {
  letter-spacing: -.02em;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 1.29;
}

.back-card-text {
  letter-spacing: -.01em;
  color: rgba(255, 255, 255, .5);
  margin-bottom: 16px;
  font-size: 14px;
  line-height: 1.29;
}

.back-card-title {
  letter-spacing: -.02em;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 1.29;
}

.back-card-list {
  letter-spacing: -.01em;
  color: rgba(255, 255, 255, .5);
  margin-bottom: 16px;
  font-size: 14px;
  line-height: 1.29;
}

.modal-container {
  padding: 66px 20px 40px;
}

.form-close-icon {
  color: #000;
}

.close-button {
  cursor: pointer;
  transform: rotate(0);
  will-change: transform;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-button:hover, .close-button:focus, .close-button:active {
  transform: rotate(90deg);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-buy-now {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-pop-up {
  box-sizing: border-box;
  height: 1073px;
  background-color: #fff;
  border-radius: 8px;
  padding: 40px 20px 22px;
  font-family: Epilogue, sans-serif;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.form-align, .modal-second {
  width: 100%;
}

.modal-first {
  margin-top: 26px;
  margin-bottom: 40px;
}

.modal-img {
  display: flex;
}

.buy-now-form {
  font-family: Epilogue, sans-serif;
}

.label-form {
  letter-spacing: -.28px;
  width: 100%;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  display: block;
}

.form-input {
  width: 100%;
  height: 42px;
  letter-spacing: -.32px;
  border: 1px solid rgba(20, 20, 20, .5);
  border-radius: 8px;
  align-self: stretch;
  align-items: flex-start;
  gap: 8px;
  margin-top: 12px;
  margin-bottom: 24px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.form-textarea {
  resize: none;
  height: 170px;
}

.send-form-button {
  background: var(--dark-green, #1e2827);
  color: #fff;
  width: 100%;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 8px;
  margin-bottom: 12px;
  padding: 17px 35px;
  font-weight: 400;
}

.form-text-policy {
  letter-spacing: -.02em;
  color: rgba(20, 20, 20, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.modal-link-policy {
  color: currentColor;
  text-decoration: underline;
}

@media screen and (max-width: 767px) {
  .catalog-container {
    padding: 40px 0;
  }
}

@media screen and (min-width: 768px) {
  .watch-list {
    gap: 40px 32px;
  }

  .modal-pop-up {
    height: auto;
    display: flex;
  }

  .form-align {
    align-items: flex-start;
    gap: 40px;
    display: flex;
  }

  .form-input {
    width: 368px;
  }
}

@media screen and (min-width: 1280px) {
  .mobile-hidden.watch-item {
    flex-direction: column;
    display: flex;
  }

  .watch-list {
    gap: 64px 24px;
  }

  .catalog-container {
    max-width: 1240px;
  }

  .modal-pop-up {
    max-width: 904px;
  }

  .form-input {
    width: 100%;
  }
}

